<template>
  <div class="container">
    <div class="card card-custom mb-3">
      <div class="card-body">
        <h4 class="card-title mb-0 font-weight-bolder mb-3">{{ config.title }}</h4>
        <global-filter inline allow-all @change="globalFilterChange($event)" />
        <hr style="border-style: dashed">
        <div class="d-flex align-items-center flex-fill">
          <span class="font-weight-bold font-size-lg pr-3" style="min-width:110px" for="">Pilih Agenda :</span>
          <v-select :clearable="false" v-model="selectedAgenda" :appendToBody="true" class="vs-style custom flex-fill" :options="agendaList" :placeholder="'Pilih salah satu Agenda'">
            <slot name="no-options">{{ $t('error.no_data') }}</slot>
          </v-select>
        </div>
      </div>
    </div>
    <div v-if="Object.keys(statistic).length" class="row mb-3">
      <div class="col-2 pr-1">
        <div class="statistic-card">
          <span class="font-size- text-muted font-weight-bolder">Total Item Template</span>
          <div class="d-flex justify-content-between align-items-center text-primary">
            <span class="font-size-h1 font-weight-bolder">{{ statistic.total_template_count || 0 }}</span>
            <i style="line-height:1" class="ri-profile-line ri-3x text-primary"></i>
          </div>
        </div>
      </div>
      <div class="col-2 px-1">
        <div class="statistic-card">
          <span class="font-size- text-muted font-weight-bolder">Open</span>
          <div class="d-flex justify-content-between align-items-center text-muted">
            <span class="font-size-h1 font-weight-bolder">{{ statistic.total_template_open_count || 0 }}</span>
            <i style="line-height:1" class="ri-profile-line ri-3x text-muted"></i>
          </div>
        </div>
      </div>
      <div class="col-2 px-1">
        <div class="statistic-card">
          <span class="font-size- text-muted font-weight-bolder">Draft</span>
          <div class="d-flex justify-content-between align-items-center text-info">
            <span class="font-size-h1 font-weight-bolder">{{ statistic.total_template_draft_count || 0 }}</span>
            <i style="line-height:1" class="ri-profile-line ri-3x text-info"></i>
          </div>
        </div>
      </div>
      <div class="col-2 px-1">
        <div class="statistic-card">
          <span class="font-size- text-muted font-weight-bolder">Menunggu Verifikasi</span>
          <div class="d-flex justify-content-between align-items-center text-warning">
            <span class="font-size-h1 font-weight-bolder">{{ statistic.total_template_verification_request_count || 0 }}</span>
            <i style="line-height:1" class="ri-profile-line ri-3x text-warning"></i>
          </div>
        </div>
      </div>
      <div class="col-2 px-1">
        <div class="statistic-card">
          <span class="font-size- text-muted font-weight-bolder">Revisi</span>
          <div class="d-flex justify-content-between align-items-center text-danger">
            <span class="font-size-h1 font-weight-bolder">{{ statistic.total_template_revision_request_count || 0 }}</span>
            <i style="line-height:1" class="ri-profile-line ri-3x text-danger"></i>
          </div>
        </div>
      </div>
      <div class="col-2 pl-1">
        <div class="statistic-card">
          <span class="font-size- text-muted font-weight-bolder">Diterima</span>
          <div class="d-flex justify-content-between align-items-center text-success">
            <span class="font-size-h1 font-weight-bolder">{{ statistic.total_template_approved_count || 0 }}</span>
            <i style="line-height:1" class="ri-profile-line ri-3x text-success"></i>
          </div>
        </div>
      </div>
    </div>
    <div class="card card-custom mb-3">
      <div class="card-body position-relative p-3">
        <apexchart v-if="barStatusSeries.length" type="bar" :height="barStatusSeries.length * 50" :options="barStatusOptions" :series="barStatusSeries"></apexchart>
      </div>
    </div>
    <div class="card card-custom mb-3">
      <div class="card-header border-0 py-5">
        <h3 class="card-title align-items-start flex-column">
          <span class="card-label font-weight-bolder text-dark">Rekap Monitoring</span>
          <span class="text-muted mt-3 font-weight-bold font-size-sm">Pengumpulan data input RKAP Kualitatif</span>
        </h3>
        <div class="card-toolbar">
          <!-- <a @click="$_sys.xlsHTML($refs['table-recapt'], 'Rekap Monitoring')" class="btn btn-success font-weight-bolder font-size-sm mr-3"> -->
          <a @click="excelAPI()" class="btn btn-success font-weight-bolder font-size-sm mr-3">
            <i class="ri-file-excel-2-line font-weight-normal"></i> Export Excel </a>
          <button v-print="printConfig" class="btn btn-danger font-weight-bolder font-size-sm mr-3"><i class="ri-printer-line font-weight-normal"></i> Cetak PDF</button>
        </div>
      </div>
      <div class="card-body pt-0 position-relative" id="print-report">
        <table id="table-recapt" ref="table-recapt" class="table table-custom-header-bg v-center bg-transparent">
          <thead>
            <tr>
              <th class="pl-0">Nama Divisi</th>
              <th class="nowrap-table text-center">Total Template</th>
              <th class="nowrap-table text-center">Open</th>
              <th class="nowrap-table text-center">Draft</th>
              <th class="nowrap-table text-center">Menunggu Verifikasi</th>
              <th class="nowrap-table text-center">Revisi</th>
              <th class="nowrap-table text-center">Diterima</th>
              <th class="nowrap-table">Batas Waktu Pengumpulan</th>
            </tr>
          </thead>
          <tbody v-if="tableStatus.length">
            <tr v-for="(d,i) in tableStatus" :key="i+'-status-table'">
              <th class="pl-0 text-primary pointer" @click="goto(d)">{{ d.name }}</th>
              <td class="text-center">{{ d.total_template_count }}</td>
              <td class="text-center">{{ d.status_code.filter(x=> x.status_code === 'open')[0].value || 0 }}</td>
              <td class="text-center">{{ d.status_code.filter(x=> x.status_code === 'draft')[0].value || 0 }}</td>
              <td class="text-center">{{ d.status_code.filter(x=> x.status_code === 'verification_request')[0].value || 0 }}</td>
              <td class="text-center">{{ d.status_code.filter(x=> x.status_code === 'revision_request')[0].value || 0 }}</td>
              <td class="text-center">{{ d.status_code.filter(x=> x.status_code === 'approved')[0].value || 0 }}</td>
              <td class="nowrap-table">{{ d.closing_date | parse('longDate') }} ({{ d.countdown }})</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'crud-monitoring',
  data () {
    return {
      config: {
        title: 'Monitoring Pengumpulan RKAP Kualitatif',
        permission: {
          create: 'create-monitoring',
          read: 'view-dashboard-monitoring-qualitatif',
          update: 'update-monitoring',
          delete: 'delete-monitoring'
        }
      },
      fullFilter: {},
      usedFilter: {},
      statistic: {},
      barStatusOptions: {
        chart: {
          height: 350,
          stacked: true,
          type: 'bar',
          events: {
            dataPointSelection: (event, chartContext, config) => {
              console.log(config)
            },
            dataPointMouseEnter: function (event) {
              event.path[0].style.cursor = 'pointer'
            }
          }
        },
        colors: ['#b5b5c3', '#247dd7', '#eeb42c', '#c44233', '#1ab45a'],
        plotOptions: {
          bar: {
            stacked: true,
            borderRadius: 4,
            horizontal: true,
            distributed: false,
            dataLabels: {
              position: 'top'
            }
          }
        },
        dataLabels: {
          enabled: true,
          offsetX: -15,
          // offsetY: -1,
          formatter: (value) => { return value ? value.toLocaleString('id-ID') : undefined },
          style: {
            fontSize: '12px',
            fontFamily: 'Helvetica, Arial, sans-serif',
            fontWeight: 'bold',
            colors: ['#fff']
          }
        },
        noData: {
          text: 'Loading Chart',
          align: 'center',
          verticalAlign: 'middle',
          offsetX: 0,
          offsetY: 0,
          style: {
            color: undefined,
            fontSize: '14px',
            fontFamily: undefined
          }
        },
        xaxis: {
          type: 'category',
          position: 'bottom'
        },
        yaxis: {
          labels: {
            formatter: (value) => { return value ? value.toLocaleString('id-ID') : undefined }
          }
        },
        tooltip: {
          shared: true,
          intersect: false,
          y: {
            formatter: (value) => { return value ? value.toLocaleString('id-ID') : undefined }
          },
          x: {
            formatter: (value) => { return value ? 'Divisi : ' + value : undefined }
          }
        },
        legend: {
          show: true
        }
      },
      barStatusSeries: [],
      tableStatus: [],
      printConfig: {
        id: 'table-recapt',
        preview: false,
        standard: 'strict'
      },
      selectedAgenda: { label: 'RKAP Kualitatif', code: null },
      agendaList: []
    }
  },
  watch: {
    selectedAgenda: {
      deep: true,
      immediate: false,
      handler (e) {
        const res = (typeof e === 'object') ? e.code : e
        this.usedFilter = Object.assign({}, this.usedFilter, {
          agenda_id: res,
          page: 1,
          limit: 10
        })
      }
    },
    usedFilter: {
      deep: true,
      immediate: false,
      handler (e) {
        this.loadSummary(e)
        this.loadChart(e)
      }
    }
  },
  methods: {
    globalFilterChange (e) {
      this.fullFilter = Object.assign({}, e)
      this.getAgenda(e, null, true)
    },
    getAgenda (e, selected = null, fromGlobal = false) {
      const tempFilter = Object.assign({}, {
        division_id: fromGlobal ? e.division_id && e.division_id.code ? e.division_id.code : null : e.division_id,
        periode_year: fromGlobal ? e.year : e.periode_year,
        agenda_id: selected || null
      })
      this.$_api.dataset('m-qualitatif-agendas', tempFilter).then(ag => {
        let agenda = ag.data.map(x => { return { label: x.name, code: x.id } })
        this.agendaList = [...[{ label: 'RKAP Kualitatif', code: null }], ...agenda]
        if (!selected) this.selectedAgenda = this.agendaList[0]
        else this.selectedAgenda = this.agendaList.filter(x => x.code === selected)[0]
        // ======
        this.usedFilter = Object.assign({}, tempFilter, {
          search: '',
          page: 1,
          limit: 10,
          id: this.$route.query.id || null
        })
      })
    },
    loadSummary (filter) {
      this.$_api.get('dashboard-qualitatif/qualitatif-accumulation-on-divisions/data', filter).then(res => {
        this.statistic = res.data
      }).catch(err => {
        this.$_alert.error(err)
      })
    },
    loadChart (filter) {
      this.$_api.get('dashboard-qualitatif/qualitatif-graph-status-on-divisions/data', filter).then(res => {
        this.tableStatus = res.data
        let status = [
          { code: 'open', name: 'Open' },
          { code: 'draft', name: 'Draft' },
          { code: 'verification_request', name: 'Menunggu Verifikasi' },
          { code: 'revision_request', name: 'Revisi' },
          { code: 'approved', name: 'Diterima' }
        ]
        let finalData = []
        for (let i = 0; i < status.length; i++) {
          const e = status[i]
          let data = res.data.map(x => { return { x: x.name, y: x.status_code.filter(a => a.status_code === e.code)[0].value } })
          finalData.push({ name: e.name, data: data })
        }
        this.barStatusSeries = finalData
      }).catch(err => {
        this.$_alert.error(err)
      })
    },
    goto (e) {
      const filter = {
        division_id: e.id,
        periode_year: e.periode_year
      }
      this.$router.push({
        name: 'pengumpulan-RKAP-kualitatif',
        query: { ...{ view: new Date().getTime() }, ...filter }
      })
    },
    excelAPI () {
      const appMode = require('@/mode.js').default
      const api = '/export-excel/dashboard-rekap-monitoring'
      let filter = '?periode_year=' + this.usedFilter.periode_year
      if (this.usedFilter.division_id) filter += '&division_id=' + this.usedFilter.division_id
      const url = this.$_config.server[appMode] + api + filter
      this.$_sys.forceDownload({ url: url, filename: 'Monitoring RKAP Tahun' + this.usedFilter.periode_year })
    }
  }
}
</script>

<style scoped>
  .statistic-card {
    display: block;
    /* min-width: calc(100% / 5 - (0.4rem)); */
    text-align: left;
    padding: 0.75rem 1rem;
    border-radius: 8px;
    background-color: #fff;
  }
  .statistic-card:not(:last-of-type) {
    /* margin-right: 0.5rem; */
  }
</style>



